.apps {
  background-color: #16202d;
  color: white;
  width: 320px;
  height: 271px;
  display: flex;
  flex-direction: column;
  align-items: center;
  padding-top: 28px;
}

.apps__download-button {
  width: 280px;
  height: 55px;
  font-family: "OpenSansBold", "Helvetica", sans-serif;
  font-size: 18px;
  border: solid 3px #ffffff;
  background-color: transparent;
  color: #ffffff;
  border-radius: 50px;
  margin-bottom: 32px;
}

.apps__items {
  width: 205px;
  display: flex;
  align-items: baseline;
  justify-content: space-between;
  margin-bottom: 31px;
}

.apps__item--apple {
  width: 47px;
  height: 56px;
}

.apps__item--android {
  width: 43px;
  height: 47px;
}

.apps__item--win {
  width: 44px;
  height: 41px;
}

.apps__info {
  font-family: "OpenSansLight", "Helvetica", sans-serif;
  font-size: 14px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: 24px;
  letter-spacing: normal;
  text-align: center;
}
